import helpers from 'api/helpers';

function saveCollage(collageID, collageDetails) {
  try {
    const path = '/api/collage/save';
    const params = { collage_id: collageID };
    const authURL = process.env.REACT_APP_AUTH_URL;
    return helpers.post(authURL, path, params, JSON.stringify(collageDetails));
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
    return error;
  }
}

export default { saveCollage };
